        <h5>上面的这个例子很简单，它能说明：对象可以通过函数来创建。对！也只能说明这一点。<br>但是我要说——对象都是通过函数创建的——有些人可能反驳：不对！因为：</h5>
        <code>
            <img src="../../static/images/2/3.png">
        </code>
        <h5>但是不好意思，这个——真的——是一种——“快捷方式”，在编程语言中，一般叫做“语法糖”。<br>做“语法糖”做的最好的可谓是微软大哥，它把他们家C#那小子弄的不男不女从的，本想图个人见人爱，谁承想还得到处跟人解释——其实它是个男孩！<br>话归正传——其实以上代码的本质是：</h5>
        <code>
            <img src="../../static/images/2/4.png">
        </code>
        <h5>而其中的 Object 和 Array 都是函数：</h5>
        <code>
            <img src="../../static/images/2/5.png">
        </code>
        <h5>所以，可以很负责任的说——对象都是通过函数来创建的。</h5>
