// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import prototypeClosure1 from './components/prototypeClouse1.vue';
import prototypeClosure2 from './components/prototypeClouse2.vue';
import prototypeClosure3 from './components/prototypeClouse3.vue';
import prototypeClosure4 from './components/prototypeClouse4.vue';
import prototypeClosure5 from './components/prototypeClouse5.vue';
import prototypeClosure8 from './components/prototypeClouse8.vue';
import prototypeClosure9 from './components/prototypeClouse9.vue';
import prototypeClosure10 from './components/prototypeClouse10.vue';
import Flex from './components/flex.vue';
import boxsizing from './components/boxsizing.vue';

document.body.style.webkitUserSelect='none';
/* eslint-disable no-new */
Vue.use(VueRouter);
Vue.use(VueResource);
var routes = [
    {path:'/prototypeClosure1',component:prototypeClosure1},
    {path:'/prototypeClosure2',component:prototypeClosure2},
    {path:'/prototypeClosure3',component:prototypeClosure3},
    {path:'/prototypeClosure4',component:prototypeClosure4},
    {path:'/prototypeClosure5',component:prototypeClosure5},
    {path:'/prototypeClosure8',component:prototypeClosure8},
    {path:'/prototypeClosure9',component:prototypeClosure9},
    {path:'/prototypeClosure10',component:prototypeClosure10},
    {path:'/flex',component:Flex},
    {path:'/boxsizing',component:boxsizing},
];
var router = new VueRouter({
    routes,
    linkActiveClass:'active'//配置激活状态是的class
});
var app = Vue.extend(App);
new app({
    router
}).$mount('#app');

router.push('/prototypeClosure1');  //默认跳转tab





